import './App.css';
import React from 'react';

class App extends React.Component {
  
  constructor(props) {
    super(props)
    this.state = {input: 0,
                  output: ""}
  this.reset = this.reset.bind(this)
  this.change = this.change.bind(this)
  this.changepoint = this.changepoint.bind(this) 
  this.changeoperator = this.changeoperator.bind(this)
  this.result = this.result.bind(this)
  this.changezero = this.changezero.bind(this)}
  

reset() {this.setState({input: 0,output: ""})}

change(event) {if (this.state.output.slice(-1) === "0" & this.state.output.length === 1)
                 {this.setState({input: event.target.value,output: event.target.value})}
                 else {this.setState({input: event.target.value,output: this.state.output + event.target.value})}}

changepoint(event) {let a = this.state.output
          if (a.slice(-1) !== event.target.value)
          {this.setState({input: event.target.value,output: this.state.output + event.target.value})}}

changeoperator(event) {let a = this.state.output
                      if (a.indexOf("=") > -1)
                         {this.setState({input: event.target.value,output: Number(this.state.input.toFixed(7)) + event.target.value})}
                       else if (event.target.value === "-" & a.slice(-1) === "/" || a.slice(-1) === "*" || a.slice(-1) === "+" || a.slice(-1) === "-") 
                                {this.setState({input: event.target.value,output: this.state.output + event.target.value})}
                       else if (a.slice(-1) !== "/" & a.slice(-1) !== "*" & a.slice(-1) !== "+" & a.slice(-1) !== "-")
                       {this.setState({input: event.target.value,output: this.state.output + event.target.value})}
                       else {this.setState({input: event.target.value, output: this.state.output.substring(0, this.state.output.length - 1) + event.target.value})}}

changezero(event) {if (this.state.output.slice(-1) !== "0") 
                   {this.setState({input: event.target.value,output: this.state.output + event.target.value})}
                   else if (this.state.output.slice(-1) === "0" & this.state.output.length > 1)
                            {this.setState({input: event.target.value,output: this.state.output + event.target.value})}
                   else {this.setState({output: this.state.output})}}

result() {let result = eval(this.state.output)
          this.setState({input: result,output: this.state.output + "=" + result})}

          render() {
  return(<div id="main">
           <div id="container">
           <p id="output">{this.state.output}</p>
             <div id="display">{this.state.input}</div>
             <button  className="button-width" id="clear" onClick={this.reset}>AC</button>
             
             <button  className="button-simple" id="seven" onClick={this.change} value="7">7</button >
             <button  className="button-simple" id="eight" value="8" onClick={this.change}>8</button >
             <button  className="button-simple" id="nine" value="9" onClick={this.change}>9</button >
             <button  className="button-operator" id="subtract" value="-" onClick={this.changeoperator}>-</button >
             <button  className="button-simple" id="four" value="4" onClick={this.change}>4</button >
             <button  className="button-simple" id="five" value="5" onClick={this.change}>5</button >
             <button  className="button-simple" id="six" value="6" onClick={this.change}>6</button >
             <button  className="button-operator" id="add" value="+" onClick={this.changeoperator}>+</button >
             <button  className="button-simple" id="one" value="1" onClick={this.change}>1</button >
             <button  className="button-simple" id="two" value="2" onClick={this.change}>2</button >
             <button  className="button-simple" id="three" value="3" onClick={this.change}>3</button >
             <button  className="button-height" id="equals" value="=" onClick={this.result}>=</button >
             <button className="button-width"  id="zero" value="0" onClick={this.changezero}>0</button >
             <button  className="button-operator" id="divide" value="/" onClick={this.changeoperator}>/</button >
             <button  className="button-operator" id="multiply" value="*" onClick={this.changeoperator}>X</button >
             <button  className="button-simple" id="decimal" value="." onClick={this.changepoint}>.</button >
            </div>
            <footer id="footer">
              <p id="name">By Majop</p>
             </footer>
         </div>)}
}

export default App;
